.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    align-content: center;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1450px;
  }
  
  .navbar-logo {
    color: #fff;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2.5rem;
    display: flex;
    height: 100%;
    align-items: center;
  }
  .navbar-logo:hover{
    text-decoration: none;
    color: #fff;
  }
  .tab{
    margin-top: 12px;
    color: #fff;
    font-family: 'PT Sans', sans-serif;
    font-size: 2.3rem;
    text-decoration: none;
    height: 55px;
  }
  

  .fa-galactic-republic {
    margin-left: 0.5rem;
    font-size: 2rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(8, auto);
    grid-gap: 0px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
    text-decoration: none;
  }
  
  .nav-item {
    margin-top: 12px;
    height: 75px;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;  
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    text-decoration: none;
  }
  .fa-bars {
    color: #fff;
  }
  .nav-links-mobile {
    display: none;
    text-decoration: none;
  }

  .nav-item-signin{
    display: none;
    text-decoration: none;
  }
  .menu-icon {
    display: none;
  }

  @media screen and (max-width: 1450px) {
    .navbar-container{
      width: 100%;
      display: flex;
      align-content: center;
      justify-content: unset;
      align-items: unset;

    }
    .nav-menu {
      flex-direction: column;
      width: 0vw;
      height: 100vh;
      position:absolute;
      top: 79px;
      right: 100px;
      transition: all 0.5s ease;
      justify-content: flex-start;
      display: flex;
      opacity: 0;
      align-items: flex-end; 
    }
    .nav-menu .nav-links{
      display: none;
    }
    .nav-menu .nav-item{
      width: 100%;
    }
    .nav-item-signin{
      display: flex;
      width: 100%;
      height: 75px;
      align-items: center;
      
    }
    .nav-menu.active {
      flex-direction: column;
      background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
      right: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      margin-right: 0rem;
      width: 50vw;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    .nav-menu.active .nav-item{
      width: 100%;
      display: flex;
      align-items: center;
    }
    .nav-menu.active .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
      font-size: 1.2rem;
    }
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      display: flex;
      position: absolute;
      align-items: center;
      top: -30px;
      left: 0;
      margin-left: 0px;
      transform: translate(25%, 50%);
      
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }